import { handleActions } from 'redux-actions';
import { SystemState } from './models';
import * as AT from '../constants/ActionTypes';

const initialState: SystemState = <SystemState> {
    prepare: true
};

export default handleActions<SystemState, {}>(
    {
        [AT.PREPARED]: (state: SystemState): SystemState => {
            const newState = Object.assign({}, state);
            newState.prepare = false;
            return newState;       
        }
    }, 
    initialState
);


