const width = 640;
const height = 480;

const loadImage = () => {
    return new Promise<any>((resolve, reject) => {
        const element = document.createElement('input') as HTMLInputElement;
        const selectedFile = (evt: Event) => {
        var file = (evt.target as HTMLInputElement).files;
        if (!file) {
            element.removeEventListener('change', selectedFile);
            return;
        }
        var reader = new FileReader();
        reader.readAsDataURL(file[0]);
        reader.onload = () => {
            const rawImg = reader.result;
            var canvas = document.createElement('canvas');
            canvas.width =  width;
            canvas.height = height;
            const ctx = canvas.getContext('2d');
            if (ctx) {
                const img = new Image();
                img.onload = () => {
                    ctx.drawImage(img, 0, 0, width, height);
                    const resultImg = canvas.toDataURL();
                    resolve(resultImg);
                };
                img.src = rawImg;
            }
            element.removeEventListener('change', selectedFile);
        };
        reader.onerror = () => {
            reject(reader.result);
            element.removeEventListener('change', selectedFile);
        };
        };
        element.addEventListener('change', selectedFile);
        element.type = 'file';
        element.accept = 'image/*;capture=camera';
        element.click();
    });
};

export default loadImage;


