import { handleActions, Action } from 'redux-actions';
import { CustomerListState } from './models';
import * as AT from '../../../constants/ActionTypes';
import { Customer } from '../../customer/models';

const initialState: CustomerListState = <CustomerListState> {
    customerList: []
};

export default handleActions<CustomerListState, Customer | Customer[]>(
    {
        [AT.UPDATE_CUSTOMER]: (state: CustomerListState, action: Action<Customer>): CustomerListState => {
            if (!action.payload) {
                return state;
            }
            const targetCustomer = action.payload;
            const newState = Object.assign({}, state);
            const customerList = Array.apply(null, newState.customerList);
            const index = newState.customerList.findIndex((c) => {return c.id === targetCustomer.id; });
            if (index < 0) {
                return state;
            }
            customerList[index] = targetCustomer;
            newState.customerList = customerList;
            return newState;
        },
        [AT.SET_CUSTOMER_LIST]: (state: CustomerListState, action: Action<Customer[]>): CustomerListState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            newState.customerList = action.payload;
            return newState;
        }
    }, 
    initialState
);


